body {
  font: 14px "Century Gothic", Futura, sans-serif; 
  /* font-family: 'Open Sans'; */
  margin: 20px;
}

.bluebar
{
  background-color: #00a7f4;
  height:14px;
}

.inputview {
  padding:15px;
  border: 1px solid #EEE;
}

button{
  margin:5px;
  padding:10px; 
  background-color:#00a7f4;
  color:white;
  border: 0px solid white;
  border-radius: 4px;
}


.inputview button:disabled {
  background-color:#aec7d3;
}

