.inputview .numberinputs input {
  text-align: center;
  padding: 6px 4px 6px 4px;
  width:40px;
  font-size:14px;
  margin-left:0px;
  disabled:true;
}

.inputview .numberinputs .number{
  float:left;
  min-width:2.5em;
  min-height:1em;
  padding-top:5px;
}

.inputview .resetbutton{
  float:right;
  margin-right: 10%;
}

.inputview .errorprompt {
  color:red;
}

/*
.inputview .numberinputs input{
	float:right;
  display: inline-block;
  margin-right: 26vw;
}*/

.inputview .submittable{
  border: 1px solid green;
}

.inputview .notsubmittable{
  border: 1px solid lightgray;
}

.inputview .disabled{
  /* border: 1px solid rgba(255, 255, 255, 0); */
  border: 1px solid rgba(255, 255, 255, 0); 
  background-color: rgb(238, 238, 238);
  pointer-events:none;
  /*color: transparent;*/
  /* tabulaattorilla valittavissa bugi */
}

.inputview .numberinputs{
  padding-top: 20px;
  padding-bottom:30px;
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: repeat(3, 33vw);
  grid-template-rows: repeat(6, 50px);
  grid-template-rows: repeat(18, 35px); /* 18 pysty sarakkeessa, liian manuaalinen */
}

.resultsView .printbutton{
  margin-top: 1em;
}

.resultsView img{
  max-width:60vw;
  max-width:800px;
}


@media print { 
  /* All your print styles go here */
  button { display: none !important; } 
  .resultsView img {width:85vw; }
 }